.header{
    margin-top: 20px;
}

.headerTitles{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Satisfy', cursive;
    color: #444;
}

.headerTitleSm{
    position: absolute;
    top: 60%;
    right: 100px;
    font-size: 40px;
    color: white;
}

.headerTitleLg{
    position: absolute;
    top: 35%;
    right: 100px;
    font-size: 100px;
    color: white;
}

.headerImg{
    width: 100%;
    height: 712px;
    object-fit: cover;
}